<template>
  <div class="Pay">
    <!-- 导航 -->
    <div class="header clearfix">
      <van-nav-bar title="购物车" left-arrow @click-left="back"></van-nav-bar>
    </div>
    <!-- 地址管理 -->
    <van-row gutter="20" class="address" @click="openPopup">
      <van-col span="3">
        <van-icon class="icon1" name="location-o" color="rgb(150,150,150)" size="28px" />
      </van-col>
      <van-col span="18">
        <div class="userAddressname fl">
          <span>收货人：</span>
          {{userAddress.receiver}}
        </div>
        <div class="userAddresstel fr">{{userAddress.phone}}</div>
        <div class="userAddressaddress fl">
          <span>收货地址：</span>
          {{userAddress.address}}
        </div>
      </van-col>
      <van-col span="2">
        <van-icon class="icon2" name="arrow" color="rgb(182,182,182)" size="22px" />
      </van-col>
    </van-row>

    <div class="order-box clearfix">
      <div class="order-content clearfix">
        <div class="order-item clearfix" v-for="(item,index) in order" :key="index">
          <div class="pro-img fl">
            <img
              v-if="item.goods_small_logo == ''"
              class="auto-img"
              :src="require('../assets/images/kzt.png')"
              alt
              srcset
            />
            <img v-else class="auto-img" :src="item.goods_small_logo" alt srcset />
          </div>
          <div class="pro-info fl">
            <div class="name">{{item.goods_name}}</div>
            <div class="type">{{item.typeName}}</div>
            <div class="price fl">￥{{item.goods_price}}</div>
            <div class="num fr">x{{item.num}}</div>
          </div>
        </div>
      </div>
      <div class="order-total clearfix">
        <div class="fr clearfix">
          <span class="fl total-count">共计 {{counts}} 件商品 合计：</span>
          <span class="fl total-price">￥{{total}}</span>
        </div>
      </div>
    </div>

    <van-submit-bar @submit="onsubmit" :price="total * 100" button-text="立即结算" button-type="info" />

    <van-popup class="popup-box" v-model="isOpen" position="bottom">
      <van-address-list
        v-model="aid"
        :list="addressList"
        default-tag-text="默认"
        @select="selectAddress"
        @add="goAddress"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 截取路由参数
      order: "",
      total: "",
      counts: "",
      isOpen: false,
      aid: "",
      addressList: [],
      userAddress: {
        address: "请选择收货地址",
        tel: "",
        phone: "",
        receiver: "请填写收货人",
      },
    };
  },
  created() {
    // 截取路由参数
    this.order = this.$route.query.order;
    this.total = this.$route.query.total;
    this.counts = this.order.length;
    // Replace
    // Replace

    this.getAddressData();
  },
  methods: {
    openPopup() {
      this.isOpen = true;
    },

    //获取地址数据
    getAddressData() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "http://www.kangliuyong.com:10002/findAddress",
        params: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 20000) {
            result.data.result.forEach((v) => {
              let currentAddress = {
                id: v.aid,
                tel: v.tel,
                name: v.name,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault),
              };

              //默认选择默认地址
              if (v.isDefault) {
                this.aid = v.aid;

                //如果存在默认地址，则默认选择默认地址
                this.userAddress.address = currentAddress.address;
                this.userAddress.receiver = currentAddress.name;
                this.userAddress.phone = currentAddress.tel;
              }

              this.addressList.push(currentAddress);
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //选择地址
    selectAddress(item) {
      this.isOpen = false;
      this.address = item.address;

      this.userAddress = {
        address: item.address,
        phone: item.tel,
        receiver: item.name,
      };
    },
    // 新增，编辑地址
    goAddress(address) {
      let o = {
        name: "Address",
      };

      if (address) {
        o.params = {
          aid: address.id,
        };
      }
      this.$router.push(o);
    },
    //提交订单
    onsubmit() {
      let a = JSON.parse(localStorage.getItem("Shopcart"));
      for (let i = 0; i < a.length; i++) {
        for (let j = 0; j < this.order.length; j++) {
          if (a[i].goods_id == this.order[j].goods_id) {
            a.splice(i, 1);
          }
        }
      }
      localStorage.setItem("Shopcart", JSON.stringify(a));
      // Replace

      // 保存订单数据
      let order = {
        orderId: new Date().getTime(),
        address: this.userAddress,
        goodsList: this.order,
        dateTime: new Date(),
        type: 2, //已支付
      };
      // 调用vuex订单数据状态
      this.$store.commit("order/add", order);
      this.$router.push("/order/orderAll");
    },
    //返回
    back() {
      let a = JSON.parse(localStorage.getItem("Shopcart"));
      for (let i = 0; i < a.length; i++) {
        for (let j = 0; j < this.order.length; j++) {
          if (a[i].goods_id == this.order[j].goods_id) {
            a.splice(i, 1);
          }
        }
      }
      localStorage.setItem("Shopcart", JSON.stringify(a));
      // 保存订单数据
      let order = {
        orderId: new Date().getTime(),
        address: this.userAddress,
        goodsList: this.order,
        dateTime: new Date(),
        type: 1, //未支付
      };
      // 调用vuex订单数据状态
      this.$store.commit("order/add", order);
      this.$router.push("/main/shopcart");
    },
  },
};
</script>

<style lang="less" scoped>
.Pay {
  // 导航
  .header {
    width: 100%;
    .van-nav-bar {
      /deep/ .van-nav-bar__title {
        font-size: 18px;
      }
      /deep/ .van-icon {
        color: black;
      }
    }
  }
  .address {
    height: 90px;
    font-size: 17px;
    background-color: white;
    box-shadow: 4px 4px 10px #dadbdb;
    margin: 10px;
    .icon1 {
      margin-top: 0.9rem;
      margin-left: 50%;
    }
    .icon2 {
      margin-top: 1rem;
    }
    .userAddressname {
      margin-top: 5%;
      span {
        font-size: 14px;
        color: rgb(161, 161, 161);
      }
    }
    .userAddresstel {
      margin-top: 5.8%;
    }
    .userAddressaddress {
      margin-top: 3%;
      font-size: 16px;
      overflow: hidden;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      span {
        font-size: 14px;
        color: rgb(161, 161, 161);
      }
    }
  }
  .popup-box {
    min-height: 300px;
    max-width: 580px;
    overflow-y: auto;
  }

  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: #75deee;
    border-color: #75deee;
    color: #fff;
  }

  /deep/ .van-tag--danger {
    background-color: #75deee;
  }

  /deep/ .van-button--danger {
    background-image: linear-gradient(-90deg, #75deee, #3296f8 97%);
    border: 0;
    border-radius: 50px;
    color: white;
  }

  /deep/ .van-address-item__edit {
    display: none;
  }

  .order-box {
    width: 90%;
    height: calc(~"100% - 95px");
    padding: 2.5%;
    margin: 6% 2.5% 25% 2.5%;
    background-color: white;
    border-bottom: 1px solid #e0e0e0;
    overflow-y: auto;
    .order-item {
      width: 100%;
      height: 100%;
      padding-bottom: 3%;
      .pro-img {
        width: 90px;
        margin-right: 5px;
        img {
          height: 90px;
        }
      }
      .pro-info {
        width: calc(~"100% - 95px");
        .name {
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .type {
          padding: 5px 0;
          font-size: 15px;
          color: #999999;
        }
        .price {
          width: 50%;
          height: 28px;
          line-height: 28px;
          font-size: 16px;
          font-weight: 600;
          color: #eb5211;
          float: left;
        }
        .num {
          width: 47%;
          height: 28px;
          line-height: 28px;
          padding-right: 3%;
          text-align: right;
          font-size: 15px;
          color: #999999;
        }
      }
    }
    .order-total {
      padding-top: 5px;
      height: 25px;
      line-height: 25px;
      .total-count {
        font-size: 13px;
      }
      .total-price {
        font-weight: 600;
        font-size: 16px;
        color: #eb5211;
      }
    }
  }
  .van-submit-bar {
    /deep/.van-submit-bar__price {
      color: #eb5211;
    }
    /deep/ .van-button {
      background-color: #75deee;
      background-image: linear-gradient(-90deg, #75deee, #3296f8);
      border: 0;
    }
  }
}
</style>